/*Aligns landing page logo and button*/
.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/*landing page logo*/
.logo {
  width: 750px; 
  height: auto;
  margin-bottom: -100px;
}

/*Landing Page Get started button*/
.get-started-button {
  padding: 20px 40px; 
  font-family: 'Lato', sans-serif; 
  font-weight: bold; 
  font-size: 16px; 
  letter-spacing: 1px;
  border: none;
  background-color: #265611; 
  color: white;
  cursor: pointer;
  border-radius: 30px;
  text-decoration: none;
}
